import React, {useCallback, useEffect, useState,} from "react";
import Styled from "styled-components";
import OvertonTable from "../../Components/Table/OvertonTable";
import LineTable from "../../Components/Table/LineTable";
import {API_URLS, getMoldList, postMoldRegister, postMoldState} from "../../Api/mes/manageMold";
import {useHistory} from "react-router-dom";
import {transferCodeToName} from "../../Common/codeTransferFunctions";
import {postOutsourcingDelete} from "../../Api/mes/outsourcing";


const CurrentContainer = () => {

    const [list, setList] = useState<any[]>([]);
    const [titleEventList, setTitleEventList] = useState<any[]>([]);
    const [eventList, setEventList] = useState<any[]>([]);
    const [detailList, setDetailList] = useState<any[]>([]);
    const [index, setIndex] = useState({mold_name: ' '});
    const [subIndex, setSubIndex] = useState({manager: ""})
    const [deletePk, setDeletePk] = useState<({ pk: string[] })>({pk: []})
    const [selectPk, setSelectPk] = useState<any>(null);
    const [selectMold, setSelectMold] = useState<any>(null);
    const [selectValue, setSelectValue] = useState<any>(null);
    const [page, setPage] = useState<PaginationInfo>({
        current: 1,
    });

    const history = useHistory()

    const indexList = {
        repair: {
            mold_name: '',
            manager: " ",
            registered: "  ",
            complete_date: "  ",
            status: ""
        }
    }


    const detailTitle = {
        repair: {
            manager: "",
            repair_content: ' ',
            status: '',
            complete_date: ' '
        },
    }

    const detaildummy = [
        {
            worker: '',
            total_count: '99,999',
            defective_count: '91',
            description: ['   .    .', '   .    .', '   .    .']
        },
    ]

    const titleeventdummy = [
        {
            Name: '',
            Width: 90,
            Link: () => history.push('/mold/repair/register')
        },
        {
            Name: '',
            Link: () => postDelete()
        }
    ]

    const eventdummy = [
        {
            Width: 98,
            Link: (v) => v.status === '' ? getComplete(v.pk) : getCancel(v.pk)
        },
    ]

    const arrayDelete = () => {
        while (true) {
            deletePk.pk.pop()
            if (deletePk.pk.length === 0) {
                break
            }
        }
    }

    const allCheckOnClick = useCallback((list) => {
        let tmpPk: string[] = []

        {
            list.length === 0 ?
                arrayDelete()
                :
                list.map((v, i) => {

                    if (deletePk.pk.indexOf(v.pk) === -1) {
                        tmpPk.push(v.pk)
                    }

                    tmpPk.map((vi, index) => {
                        if (deletePk.pk.indexOf(v.pk) === -1) {
                            deletePk.pk.push(vi)
                        }
                    })

                    if (tmpPk.length < deletePk.pk.length) {
                        deletePk.pk.shift()
                    }

                    console.log(deletePk.pk)

                })
        }
    }, [deletePk])

    const checkOnClick = useCallback((Data) => {
        let IndexPk = deletePk.pk.indexOf(Data.pk)
        {
            deletePk.pk.indexOf(Data.pk) !== -1 ?
                deletePk.pk.splice(IndexPk, 1)
                :
                deletePk.pk.push(Data.pk)
        }
    }, [deletePk])

    const onClick = useCallback((mold) => {
        console.log('dsfewfewf', mold.pk, mold.mold_name);
        if (mold.pk === selectPk) {
            setSelectPk(null);
            setSelectMold(null);
            setSelectValue(null);
        } else {
            setSelectPk(mold.repair_pk);
            setSelectMold(mold.mold_name);
            setSelectValue(mold)
            //TODO: api 
            getData(mold.pk)
        }
    }, [list, selectPk]);


    const getComplete = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['repair'].complete}`
        const res = await postMoldState(tempUrl, {pk: pk})

        setDetailList(res)
        getList()
    }, [detailList])

    const getCancel = useCallback(async (pk) => {
        //TODO: 
        const tempUrl = `${API_URLS['repair'].cancel}`
        const res = await postMoldState(tempUrl, {pk: pk})

        setDetailList(res)
        getList()
    }, [detailList])

    const getData = useCallback(async (pk) => {
        //TODO: 
        console.log(pk)
        const tempUrl = `${API_URLS['repair'].detail}?pk=${pk}`
        const res = await getMoldList(tempUrl)

        console.log([res])

        const Detail = [res].map((v, i) => {
            const status = v.status === 'WAIT' ? "" : ""

            return {...v, status: status}
        })

        setDetailList(Detail)

    }, [detailList, selectPk])

    const getList = useCallback(async () => { // useCallback
        //TODO: 
        const tempUrl = `${API_URLS['repair'].list}?page=${page.current}&keyword=&type=0&limit=15`
        const res = await getMoldList(tempUrl)

        const getStock = res.info_list.map((v, i) => {
            const status = v.status === 'WAIT' ? "" : ""

            return {...v, status: status}
        })

        setList(getStock)

        setPage({current: res.current_page, total: res.total_page})
    }, [list])

    const postDelete = useCallback(async () => {
        if (deletePk.pk.length <= 0) {
            alert('   .')
            return
        }
        const tempUrl = `${API_URLS['repair'].delete}`
        const res = await postMoldRegister(tempUrl, deletePk)

        arrayDelete()
        getList()
    }, [deletePk])

    useEffect(() => {
        getList()
    }, [page.current])


    useEffect(() => {
        console.log(detailList)
    }, [detailList])

    useEffect(() => {
        getList()
        setIndex(indexList["repair"])
        // setList(dummy)
        setDetailList(detaildummy)
        setEventList(eventdummy)
        setTitleEventList(titleeventdummy)
        setSubIndex(detailTitle["repair"])
    }, [])

    return (
        <div>
            <OvertonTable
                title={'  '}
                titleOnClickEvent={titleEventList}
                allCheckOnClickEvent={allCheckOnClick}
                checkOnClickEvent={checkOnClick}
                indexList={index}
                valueList={list}
                EventList={eventList}
                clickValue={selectValue}
                buttonState={true}
                currentPage={page.current}
                totalPage={page.total}
                pageOnClickEvent={(event, i) => setPage({...page, current: i})}
                mainOnClickEvent={onClick}>
                {
                    selectPk !== null ?
                        <LineTable title={selectMold + '  '} contentTitle={subIndex} contentList={detailList}>
                            <Line/>
                        </LineTable>
                        :
                        null
                }
            </OvertonTable>
        </div>
    );
}

const Line = Styled.hr`
    margin: 10px 20px 12px 0px;
    border-color: #353b48;
    height: 1px;
    background-color: #353b48;
`

export default CurrentContainer
