import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BASE_URL, BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer';
import NormalInput from '../../Components/Input/NormalInput';
import RegisterButton from '../../Components/Button/RegisterButton';
import {getToken} from '../../Common/tokenFunctions';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions';
import CustomIndexInput from '../../Components/Input/CustomIndexInput';
import AddInput from '../../Components/Input/AddInput';
import FullAddInput from '../../Components/Input/FullAddInput';
import TextList from '../../Components/List/TextList';
import SearchInput from '../../Components/Input/SearchInput';
import SearchModalContainer from '../../Containers/SearchModalContainer';
import AddList from '../../Components/List/AddList';
import NormalNumberInput from '../../Components/Input/NormalNumberInput';

interface IInfo {
  title: string,
  value: string,
}

//   
// ! isUpdate true     
const RegisterProduct = () => {

  const [pk, setPk] = useState<string>('');
  const [isUpdate, setIsUpdate] = useState<boolean>(false);
  const [code, setCode] = useState<string>('');
  const [spec, setSpec] = useState<string>('');
  const [name, setName] = useState<string>('');
  const [info, setInfo] = useState<IInfo[]>([]);
  const [amount, setAmount] = useState<number>(0);

  //
  const [isPoupup, setIsPoupup] = useState<boolean>(false);
  const [isSearched, setIsSearched] = useState<boolean>(false);
  const [keyword, setKeyword] = useState<string>('');
  const [checkList, setCheckList] = useState<IMold[]>([]);
  const [list, setList] = useState<IMold[]>([]);
  const [searchList, setSearchList] = useState<IMold[]>([]);

  useEffect(()=>{

    if(getParameter('pk') !== "" ){
      setPk(getParameter('pk'))
      ////alert(`   - pk :` + param)
      setIsUpdate(true)
      getData()
    }

  },[])

  /**
   * getData()
   *     
   * @param {string} url  
   * @param {string} pk  pk
   * @returns X
   */
  const getData = useCallback(async()=>{

    const res = await getRequest(BASE_URL + '/api/v1/product/view/' + getParameter('pk'), getToken(TOKEN_NAME))

    if(res === false){
      //TODO:  
    }else{
      if(res.status === 200){
         const data = res.results;
         setName(data.product_name);
         setList(data.molds);
         setCode(data.product_code);
         setSpec(data.product_spec);
         setPk(data.pk);
         setAmount(data.stock)
         setInfo(data.info_list);
      }else if(res.status === 1001 || res.data.status === 1002){
        //TODO:    
      }else{
        //TODO:   
      }
    }
  },[pk, list, code, info, spec, name]);

  /**
   * onsubmitForm()
   *   
   * @param {string} url  
   * @param {string} name 
   * @param {array} info  
   * @param {string} list  
   * @param {string} code 
   * @returns X
   */
  const onsubmitForm = useCallback(async(e)=>{
    e.preventDefault();
     //TODO: 
    ////alert(' :  - ' + code + name + info + list + spec + info );
    //return;
    if(name === "" ){
      //alert("  .  .")
      return;
    }
    let pks = new Array();
    list.forEach((v:IMold, i)=>{
        pks[i] = v.pk
    })
    const data = {
      product_name: name,
      product_code: code,
      product_spec: spec,
      stock: amount,
      molds: pks,
      info_list : info
    }

    console.log(pks)

    const res = await postRequest(BASE_URL + '/api/v1/product/register' , data, getToken(TOKEN_NAME))

    if(res === false){
      //alert('.   .')
    }else{
      if(res.status === 200){
         //alert('  ')
         setPk('')
         setName('');
         setAmount(0)
         setCode('');
         setSpec('');
         setList([]);
        setInfo([]);

      }else{
        //alert('.   .')
      }
    }


  },[list, code, name, spec,info, pk])


  /**
   * onsubmitFormUpdate()
   *   
   * @param {string} url  
   * @param {string} pk pk
   * @param {string} name 
   * @param {array} info  
   * @param {string} list 
   * @param {string} code 
   * @returns X
   */
  const onsubmitFormUpdate = useCallback(async(e)=>{
    e.preventDefault();
     //TODO: 
    ////alert(' :  - ' + pk +  code + name + info + list + spec + info );
    //return;
    if(name === "" ){
      //alert("  .  .")
      return;
    }
    let pks = new Array();
    list.forEach((v:IMold, i)=>{
        pks[i] = v.pk
    })
    const data = {
      pk: getParameter('pk'),
      product_name: name,
      product_code: code,
      product_spec: spec,
      stock: amount,
      molds: pks,
      info_list : info
    }

    const res = await postRequest(BASE_URL + '/api/v1/product/update' , data, getToken(TOKEN_NAME))

    if(res === false){
      //alert('.   .')
    }else{
      if(res.status === 200){
         //alert('  ')

      }else{
        //alert('.   .')
      }
    }

  },[list, code, name, spec, info, pk, amount])


  /**
   * onClickSearch()
   *   
   * @param {string} url  
   * @param {string} keyword  
   * @returns X
   */
  const onClickSearch = useCallback(async(e)=>{

    e.preventDefault();
    if(keyword  === '' || keyword.length < 2){
      //alert('2   ')

      return;
    }
    setIsSearched(true)

    const res = await getRequest(BASE_URL + '/api/v1/mold/search?keyword=' + keyword, getToken(TOKEN_NAME))

    if(res === false){
      //TODO:  
    }else{
      if(res.status === 200){
         const results = res.results;

         setSearchList(results);
      }else{
        //TODO:   
      }
    }
  },[keyword])


  return (
      <DashboardWrapContainer>

        <InnerBodyContainer>
            <Header title={isUpdate ? ' ' : ' '}/>
            <WhiteBoxContainer>
             <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm} >
             <NormalInput title={' '} value={name} onChangeEvent={setName} description={' '} />
             <NormalInput title={' '} value={code} onChangeEvent={setCode} description={' '} />
             <NormalInput title={''} value={spec} onChangeEvent={setSpec} description={' '} />
             <NormalNumberInput title={' '} value={amount} onChangeEvent={setAmount} description={' '} />


             {/*    +   */}
             <AddInput title={' '} icType="solo" onChangeEvent={()=>{
                  setIsPoupup(true);
                  setCheckList(list);
                  setKeyword('')}
                  }>
                {
                  list.map((v: IMold, i)=>{
                    return (
                        <TextList key={i} onClickEvent={()=>{
                          const tempList = list.slice()
                          const idx = list.indexOf(v)
                          tempList.splice(idx, 1)
                          setList(tempList)
                        }}
                        title={v.mold_code !== undefined ? v.mold_code : ''} name={v.mold_name}/>
                    )
                  })
                }
                </AddInput>


             {/*    */}
             <FullAddInput title={' '} onChangeEvent={()=>{
               const tempInfo = info.slice();
               tempInfo.push({title:`  ${info.length + 1}`, value:""});
               setInfo(tempInfo)
             }}>
              {
                info.map((v: IInfo, i)=>{
                  return(
                      <CustomIndexInput index={i} value={v}
                      onRemoveEvent={()=>{
                        const tempInfo = info.slice();
                        tempInfo.splice(i, 1)
                        setInfo(tempInfo)
                      }}
                      onChangeEvent={(obj: IInfo)=>{
                        const tempInfo = info.slice();
                        tempInfo.splice(i, 1, obj)
                        setInfo(tempInfo)
                      }}
                      />
                  )
                })
              }
              </FullAddInput>

              <RegisterButton name={isUpdate ? '' : ''} />
              </form>
            </WhiteBoxContainer>
            {/*  */}
            <SearchModalContainer
              onClickEvent={ //  
                ()=>{
                setIsPoupup(false);
                setList(checkList);
                setKeyword('')}
            }
            isVisible={isPoupup} onClickClose={()=>{setIsPoupup(false)}} title={' '} >
              <SearchInput description={' '} value={keyword} onChangeEvent={(e)=>setKeyword(e.target.value)} onClickEvent={onClickSearch}/>
                <div style={{width: '100%', marginTop:20}}>
                  {
                    isSearched ?
                    searchList.map((v: IMold, i)=>{
                      return (

                          <AddList key={i} pk={v.mold_name} dim={false} selected={checkList.find((k)=> k.pk === v.pk)? true : false }
                            onClickEvent={()=>{
                              const tempList = checkList.slice()
                              if(checkList.find((k, index)=> k.pk === v.pk) ){
                                  const idx = checkList.indexOf(v)
                                  tempList.splice(idx, 1)
                                  setCheckList(tempList)
                              }else{
                                  tempList.splice(0, 0, v)
                                  setCheckList(tempList)
                              }
                            }}
                            title={v.mold_code !== undefined ? v.mold_code : ''} name={v.mold_name}
                          />
                        )
                    })
                    :
                    null
                  }
                </div>
            </SearchModalContainer>
        </InnerBodyContainer>
      </DashboardWrapContainer>

  );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default RegisterProduct;
