import React, {useCallback, useEffect, useState} from 'react';
import Styled from 'styled-components'
import {BG_COLOR_SUB2, TOKEN_NAME} from '../../Common/configset'
import DashboardWrapContainer from '../../Containers/DashboardWrapContainer';
import Header from '../../Components/Text/Header';
import WhiteBoxContainer from '../../Containers/WhiteBoxContainer';
import NormalInput from '../../Components/Input/NormalInput';
import RegisterButton from '../../Components/Button/RegisterButton';
import NormalFileInput from '../../Components/Input/NormalFileInput';
import {getToken} from '../../Common/tokenFunctions';
import InnerBodyContainer from '../../Containers/InnerBodyContainer';
import DropdownInput from '../../Components/Input/DropdownInput';
import {getParameter, getRequest, postRequest} from '../../Common/requestFunctions';
import {uploadTempFile} from '../../Common/fileFuctuons';
import {getMachineTypeList} from '../../Common/codeTransferFunctions';
import DateInput from '../../Components/Input/DateInput';
import moment from 'moment';
import ListHeader from '../../Components/Text/ListHeader';
import OldFileInput from '../../Components/Input/OldFileInput';
import BasicSearchContainer from '../../Containers/Basic/BasicSearchContainer';
import {JsonStringifyList} from '../../Functions/JsonStringifyList';
import NormalNumberInput from '../../Components/Input/NormalNumberInput';
import {useHistory} from 'react-router-dom';
import {SF_ENDPOINT} from "../../Api/SF_endpoint";

const docDummy = [
    {pk: 'qfqwf', name: ' 1'},
    {pk: 'ehki', name: ' 2'},
    {pk: 'qfqw412f', name: ' 3'},
    {pk: 'efgrhtjyu', name: ' 4'},
    {pk: 'kmcd', name: ' 5'},
]
//   
// ! isUpdate true     
const BasicMachineRegister = () => {
    const history = useHistory();

    const [document, setDocument] = useState<any>({pk: '', value: '()'});


    const [essential, setEssential] = useState<any[]>([]);
    const [optional, setOptional] = useState<any[]>([]);

    const [pk, setPk] = useState<string>('');
    const [made, setMade] = useState<string>('');
    const [info, setInfo] = useState<string>('');
    const [infoList, setInfoList] = useState<IInfo[]>([]);
    const [name, setName] = useState<string>('');
    const [type, setType] = useState<number>(1); //1: 
    const [madeNo, setMadeNo] = useState<string>('');
    const [photoName, setPhotoName] = useState<string>('');
    const [factory, setFactory] = useState<any[]>([]);
    const [volt, setVolt] = useState<number>(0);
    const [tons, setTons] = useState<number>(0);
    const [files, setFiles] = useState<any[3]>([null, null, null]);
    const [paths, setPaths] = useState<any[3]>([null, null, null]);
    const [oldPaths, setOldPaths] = useState<any[3]>([null, null, null]);
    const [date, setDate] = useState<string>(moment().format('YYYY-MM-DD'));
    const [isUpdate, setIsUpdate] = useState<boolean>(false);

    const indexList = getMachineTypeList('kor');

    useEffect(() => {

        if (getParameter('pk') !== "") {
            setPk(getParameter('pk'))
            ////alert(`   - pk :` + param)
            setIsUpdate(true)
            getData()
        }

    }, [])


    /**
     * addFiles()
     *  
     * @param {object(file)} event.target.files[0] 
     * @returns X
     */
    const addFiles = async (event: any, index: number): Promise<void> => {
        console.log(event.target.files[0]);
        console.log(index)
        if (event.target.files[0] === undefined) {

            return;
        }
        console.log(event.target.files[0].type);
        if (event.target.files[0].type.includes('image')) { // 

            const tempFile = event.target.files[0];
            console.log(tempFile)
            const res = await uploadTempFile(event.target.files[0]);

            if (res !== false) {
                console.log(res)
                const tempPatchList = paths.slice()
                tempPatchList[index] = res;
                console.log(tempPatchList)
                setPaths(tempPatchList)
                return
            } else {
                return
            }

        } else {

            //alert('   .')
        }

    }


    const getData = useCallback(async () => {

        const res = await getRequest(`${SF_ENDPOINT}/api/v1/machine/load?pk=` + getParameter('pk'), getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
        } else {
            if (res.status === 200 || res.status === "200") {
                const data = res.results;
                setName(data.machine_name);
                setMade(data.manufacturer);
                setPhotoName(data.photo);
                setDate(data.manufactured_at);
                setPk(data.pk);
                setFactory([{pk: data.location_pk, name: data.location_name}])
                setMadeNo(data.manufacturer_code);
                setType(Number(data.machine_type));
                setInfoList(data.info_list);
                setVolt(data.volt ?? 0);
                setTons(data.tons ?? 0);
                const tempList = oldPaths.slice();
                tempList[0] = data.photo;
                tempList[1] = data.qualification;
                tempList[2] = data.capacity;
                setOldPaths(tempList);


            } else {
                //TODO:   
            }
        }
    }, [pk, made, madeNo, date, volt, tons, type, photoName, name, oldPaths, infoList, paths, essential, optional, factory])


    const onsubmitFormUpdate = useCallback(async (e) => {
        e.preventDefault();

        if (name.trim() === "") {
            alert("  .  .")
            return;
        } else if (type === 0) {
            alert("   .  .")
            return
        } else if (madeNo.trim() === "") {
            alert("   .  .")
            return;
        } else if (volt.toString() === "" || volt < 0) {
            alert("  .  .")
            return;
        } else if (factory[0]?.pk === "" || factory[0]?.pk === undefined) {
            alert("  .  .")
            return;
        } else if (tons < 0) {
            alert("    .")
            return;
        }

        const data = {
            pk: getParameter('pk'),
            machine_name: name,
            machine_type: type,
            manufacturer: !made ? made : made.trim(),
            manufacturer_code: madeNo,
            manufactured_at: date,

            location: factory[0].pk,
            info_list: JsonStringifyList(essential, optional),
            photo: paths[0],
            qualification: paths[1],
            capacity: paths[2],
            tons: tons,
            volt: volt,

        };

        const res = await postRequest(`${SF_ENDPOINT}/api/v1/machine/update/`, data, getToken(TOKEN_NAME))

        if (res === false) {
            ////alert('////alert('[SERVER ERROR]     .')')
        } else {
            if (res.status === 200) {
                //alert('  ')
                history.push(`/basic/list/machine`);
            } else {
                ////alert('      .')
            }
        }

    }, [pk, made, madeNo, name, volt, tons, type, date, madeNo, infoList, paths, essential, optional, factory])

    /**
     * onsubmitForm()
     *   
     */
    const onsubmitForm = useCallback(async (e) => {
        e.preventDefault();

        console.log('name trim', name.trim())
        //console.log(infoList)
        ////alert(JSON.stringify(infoList))
        //console.log(JSON.stringify(infoList))
        if (name.trim() === "") {
            alert("  .  .")
            return;
        } else if (type === 0) {
            alert("   .  .")
            return
        } else if (madeNo.trim() === "") {
            alert("   .  .")
            return;
        } else if (volt.toString() === "" || volt < 0) {
            alert("  .  .")
            return;
        } else if (factory[0]?.pk === "" || factory[0]?.pk === undefined) {
            alert("  .  .")
            return;
        } else if (tons < 0) {
            alert("    .")
            return;
        }

        const data = {
            document_pk: document.pk,
            machine_name: name,
            machine_type: type,
            manufacturer: !made ? made : made.trim(),
            manufacturer_code: madeNo,
            manufactured_at: date,
            location: factory[0].pk,
            info_list: JsonStringifyList(essential, optional),
            photo: paths[0],
            qualification: paths[1],
            capacity: paths[2],
            tons: tons,
            volt: volt,
        };


        const res = await postRequest(`${SF_ENDPOINT}/api/v1/machine/register`, data, getToken(TOKEN_NAME))

        if (res === false) {
            //TODO:  
            ////alert('////alert('[SERVER ERROR]     .')')
        } else {
            if (res.status === 200) {
                //alert('  ')
                history.push(`/basic/list/machine`);

            } else {
                //TODO:   
                // //alert('    .')
            }
        }

    }, [pk, made, madeNo, volt, tons, document, date, name, type, madeNo, infoList, paths, essential, optional, factory])


    return (
        <DashboardWrapContainer index={'basic'}>

            <InnerBodyContainer>
                <Header title={isUpdate ? ' ' : ' '}/>
                <WhiteBoxContainer>
                    {
                        // document.pk !== '' || isUpdate == true?
                        <form onSubmit={isUpdate ? onsubmitFormUpdate : onsubmitForm}>
                            <ListHeader title=" "/>
                            <NormalInput title={' '} value={name} onChangeEvent={setName}
                                         description={'    '}/>
                            <DropdownInput title={' '} target={indexList[type]} contents={indexList}
                                           onChangeEvent={(v) => setType(v)}/>

                            <DateInput title={' '} description={""} value={date} onChangeEvent={setDate}/>
                            <NormalInput title={'() '} value={madeNo} onChangeEvent={setMadeNo}
                                         description={'      ( )'}/>

                            <NormalNumberInput title={''} value={volt} onChangeEvent={setVolt}
                                               description={' '}/>

                            <BasicSearchContainer
                                title={''}
                                key={'pk'}
                                value={'name'}
                                onChangeEvent={
                                    (input) => {
                                        setFactory(input)
                                    }
                                }
                                option={1}
                                solo={true}
                                list={factory}
                                searchUrl={`${SF_ENDPOINT}/api/v1/factory/search?`}
                            />
                            <br/>
                            <ListHeader title=" "/>
                            <NormalInput title={''} value={made} onChangeEvent={setMade}
                                         description={'  '}/>
                            {
                                type == 1 &&
                                <>
                                    {/*<NormalNumberInput title={' '} value={slip_angle} onChangeEvent={setSlip_angle} description={''} />*/}
                                    <NormalNumberInput title={'  '} value={tons} onChangeEvent={setTons}
                                                       description={'   .'}/>
                                </>
                            }
                            <NormalFileInput title={' '} name={paths[0]} thisId={'machinePhoto0'}
                                             onChangeEvent={(e) => addFiles(e, 0)}
                                             description={isUpdate ? oldPaths[0] : '   '}
                                             style={{width: 'calc(100% - 124px)'}}/>
                            <NormalFileInput title={' '} name={paths[1]} thisId={'machinePhoto1'}
                                             onChangeEvent={(e) => addFiles(e, 1)}
                                             description={isUpdate ? oldPaths[1] : '   ( )   '}
                                             style={{width: 'calc(100% - 124px)'}}/>
                            <NormalFileInput title={' '} name={paths[2]} thisId={'machinePhoto2'}
                                             onChangeEvent={(e) => addFiles(e, 2)}
                                             description={isUpdate ? oldPaths[2] : '   ( )   '}
                                             style={{width: 'calc(100% - 124px)'}}/>
                            {
                                isUpdate ?
                                    <OldFileInput title={'  '} urlList={oldPaths}
                                                  nameList={['', '', '']}
                                                  isImage={true}/>

                                    :
                                    null
                            }
                            <br/>
                            {/*<DocumentFormatInputList*/}
                            {/*  pk={!isUpdate ? document.pk : undefined}*/}
                            {/*  loadDataUrl={isUpdate? `http://255.255.255.255:8299/api/v1/machine/load?pk=${pk}` :''}*/}
                            {/*  onChangeEssential={setEssential} onChangeOptional={setOptional}*/}
                            {/*  />*/}


                            <RegisterButton name={isUpdate ? '' : ''}/>
                        </form>
                        // :
                        // <SelectDocumentForm category={0} onChangeEvent={setDocument}/>

                    }
                </WhiteBoxContainer>

            </InnerBodyContainer>

        </DashboardWrapContainer>

    );
}
const FullPageDiv = Styled.div`
  width: 100%;
  height: 100%;
  color: white;
  background-color: ${BG_COLOR_SUB2}
`


export default BasicMachineRegister;
