import client from '../configs/basic';

/**
 * postProcessDelete()
 *   
 * @param {string} url  
 * @param object
 * @returns {object} data object
 * @author 
 * @version 0.1
 */
export const postProcessDelete = async (url: string, object: object) => {
    const temp: IServerData = await client.post(url, object);
    console.log(temp.status);
    if (temp.status === 400) {
        alert(' .')
        return
    }
    return temp.status;
}

